import PressPullRefresh from 'press-ui/press-pull-refresh/press-pull-refresh.vue';
import PressTab from 'press-ui/press-tab/press-tab';
import PressTabs from 'press-ui/press-tabs/press-tabs';


export default {
  i18n: {
    'zh-CN': {
      try: '下拉试试',
      text: '刷新次数',
      success: '刷新成功',
      successTip: '成功提示',
      customTips: '自定义提示',
    },
    'en-US': {
      try: 'Try it down',
      text: 'Refresh Count',
      success: 'Refresh success',
      successTip: 'Success Tip',
      customTips: 'Custom Tips',
    },
  },
  components: {
    PressPullRefresh,
    PressTab,
    PressTabs,
  },
  data() {
    return {
      count: 0,
      isLoading: false,
      active: 0,
    };
  },
  computed: {
    tips() {
      if (this.count) {
        return `${this.t('text')}: ${this.count}`;
      }

      return this.t('try');
    },
  },

  methods: {
    onRefresh() {
      setTimeout(() => {
        this.onGTip('刷新成功');
        this.isLoading = false;
        this.count += 1;
      }, 1000);
    },
    onChange(value) {
      console.log('[onChange]', value);
    },
    onChangeTab(value) {
      console.log('[onChangeTab]', value);
    },
  },
};
